.register {
  /*background-color: #FAFAFA;*/
  background: linear-gradient(106.65deg, #000000 0%, #283618 100%);
}

.register__column {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: auto;
}

.register__header {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 52px;  
  margin-top: 48px;
}

@media screen and (max-width: 740px) {
  .register__column {
    width: 260px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 740px) and (max-width: 1262px) {
  .register__column {
    width: 396px;
    padding-bottom: 232px;
  } 
}

@media screen and (min-width: 1262px) {
  .register__column {
    width: 396px;
    padding-bottom: 70px;
  } 
}

@media screen and (max-width: 740px) {
  .register__air-block {
    height: 157px;
  }
}

@media screen and (min-width: 740px) and (max-width: 1262px) {
  .register__air-block {
    height: 69px;
  } 
}

@media screen and (min-width: 1262px) {
  .register__air-block {
    height: 69px;
  } 
}